import * as React from 'react';

import styles from './Tabs.module.scss';

export interface ITabsProps {
  tabs: { [id: string]: string };
  selectedId: string;
  selectionHandler: (id: string) => void;
}

export default class Tabs extends React.Component<ITabsProps, void> {
  public render(): React.ReactElement<ITabsProps> {
    const tabs: React.ReactElement<{}>[] = Object.keys(this.props.tabs).map((id: string): React.ReactElement<{}> => {
      const selected: boolean = id === this.props.selectedId;
      return  <button key={id}
                      className={selected ? styles.selectedTabButton : styles.tabButton}
                      onClick={() => this.props.selectionHandler(id)}>
                {this.props.tabs[id]}
              </button>;
    });

    return  <div className={styles.container}>
              {tabs}
            </div>;
  }
}